<template>
	<view class="flex flex-direction padding">
		<input placeholder="请输入userId"  v-model="toId"  type="number" />
		<button class="cu-btn bg-cyan" @click="invite">邀请</button>
	</view>
</template>

<script>
	/**
	 * 邀请加入组织
	 * @date 2021.07.23 重新引用login
	 */
	var _self;
	export default {
		data() {
			return {
				Token : this.global.login.getToken(),			// 当前getToken
				UserId: this.global.login.getUserId(),			// 当前用户id
				MchId : this.global.login.getcurrentMchId() ,// (当前)组织Id
				// 与系统有关参数
				toId : '',		// 邀请的用户名
			}
		},
		// 初始化方法
		onLoad:function(){
			_self = this;
		},
		methods: {
			invite(e){
				uni.request({
					url: _self.website + '/api/SvcMerchant/Invitation',
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					data:{
						'invType':0,			// 邀请类型： 0 ：平台邀请 ，1 : 被邀请人手机号
						'mchId': _self.MchId,
						'fromId': _self.UserId,
						'toId': _self.toId,
						'msg':'邀请'
					},
					method: 'POST',
					success:function(result){
						uni.showToast({
							title:'邀请成功'
						});
						console.log("成功",result);
					},
					fail:function(erros){
						console.log("失败");
					},
				})
			}
		}
	}
</script>

